import Bounced from '../public/bounced.vue'
import Foot from "../public/foot.vue"
import Detail from "../public/detail.vue"
import Head from "../public/head.vue"
import None from '../public/noNumber'
import loadingImg from '../public/loadingImg'
   export default {
      name: "wish",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          isCreate:false,
          wishCreate:{
            wishCreat:'',
            wishChoose:'',
          },
          wishList:[],
          deelIndex:false,
          cancel:'删除',
          isXin: false,
          reName:'',
          newChoose:'',
          options:[
            {value:0,label:'共享'},
            {value:1,label:'私密'}
          ],
          isRena:false,
          group_id:''
        }
    },
    components:{
        Head,
      Detail,
      Foot,
      Bounced,
      None,
      loadingImg
    },
    methods:{
      createWish(){
        this.isCreate = true
      },
      closeCreat(){
        this.isCreate = false
      },
      saveWish(){
        if(this.wishCreate.wishCreat == ''||this.wishCreate.wishCreat.split(' ').join('').length == 0){
          this.$message({
            type: 'error',
            message: '保存心愿单失败'
          })
        }else{
          this.$http.post(this.api + '/home/Comment/add_collegroup',{
            token: localStorage.getItem('token'),
            group_name: this.wishCreate.wishCreat,
            hide: this.wishCreate.wishChoose
          })
            .then(res=>{
              if(res.data.code == 1){
                this.getGroup()
                this.wishCreate.wishCreat = ''
                this.wishCreate.wishChoose = ''
                this.isCreate = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.saveWish()
                  })
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        }

      },
      toList(item,index){
        this.$router.push({
          path: '/wishPage',
          query:{
            group_id: item.group_id,
            name: item.group_name
          }
        })
      },
      deel(item,index){
        this.$http.post(this.api + '/GroupD',{
          token:localStorage.getItem('token'),
          group_id: item.group_id,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.wishList.splice(index,1)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.deel(item,index)
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      deelList(){
        this.deelIndex = !this.deelIndex
      },
      getGroup(){
        this.$http.post(this.api + '/home/Comment/collegroup_list',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              if(res.data.data.length){
                this.isXin = false
              }else{
                this.isXin = true
              }
              this.wishList = res.data.data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getGroup()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      toRename(item,index){
        console.log(item,index)
        this.group_id = item.group_id
        this.isRena = true
      },
      abolishName(){
        this.isRena = false
        this.reName = ''
        this.newChoose = ''
      },
      reNameCheck(){
        this.$http.post(this.api + '/home/Comment/add_collegroup',{
          token: localStorage.getItem('token'),
          group_id: this.group_id,
          group_name: this.reName,
          hide: this.newChoose
        })
          .then(res=>{
            if(res.data.code == 1){
              this.isRena = false
              this.reName = ''
              this.newChoose = ''
              this.$message({
                type: 'success',
                message:'修改成功！'
              })
              this.getGroup()
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.reNameCheck()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      }
    },
    watch:{
      deelIndex:function(){
        if(this.deelIndex == true){
          this.cancel = '取消'
        }else{
          this.cancel = '删除'
        }
      }
    },
    mounted() {
        let _this = this
      this.getGroup();
    }
  }



